        //   .get(this._url2 + url, {
        //     type: "everyday"
        //   })
        //   .then(res => {
        //     console.log(res);
        //   });

        // 请求数据
        let date = new Date();
        date = date.getFullYear() + "-" + (date.getMonth() + 1);
        this.getDailyData(date);
      } else {
        alert("您没有权限进入这个页面");
        this.$router.push("/Login");
      }
    });
  },
  methods: {
    cellStyle({ row, column, rowIndex, columnIndex }) {
      // console.log(column);
      if (column.label.includes("营业额")) {
        return {
          background: "#ddd"
        };
      }
    },
    queryData() {
      if (!this.form.channel || !this.form.monthPick) {
        alert("请选择渠道和日期进行查询！");
        return;
      }
      console.log(this.form);
      // 发送查询请求
      this.getDailyData(this.form.monthPick);
    },
    change() {},
    getDailyData(date) {
      this.loading = true;
      this.$axios
        .get(this._url2 + "server/databases/get_everyday_report/", {
          params: {
            date
          }
        })
        .then(res => {
          this.zr = res.data.data.zr;
          this.zr2 = res.data.data.zr2;
          this.tableData = this.zr.concat(this.zr2);
          this.loading = false;
        });
    }
